import { Injectable } from '@angular/core';
import { BaseApi } from '../classes/base-api';
import { HttpClient } from '@angular/common/http';
import { BehaviorSubject, combineLatest, Observable } from 'rxjs';
import { map, switchMap } from 'rxjs/operators';
import { KPI } from '../models/kpi';

@Injectable({
  providedIn: 'root',
})
export class ApiKpiService extends BaseApi {
  organizationId$ = new BehaviorSubject(1);
  locationId$ = new BehaviorSubject(1);
  stringDateRange$ = new BehaviorSubject({ start: null, end: null });

  constructor(private http: HttpClient) {
    super();
  }

  get$ = combineLatest(
    this.organizationId$,
    this.locationId$,
    this.stringDateRange$
  ).pipe(
    switchMap(([organizationId, locationId, stringDateRange]) =>
      this.get(organizationId, locationId, stringDateRange)
    )
  );

  get(
    organizationId: number,
    locationId: number,
    stringDateRange: { start: string; end: string }
  ): Observable<KPI> {
    const url = `${this.baseUrl}/kpivalues`;
    const request = {
      OrgID: organizationId,
      LocationID: locationId,
      StartDate: stringDateRange.start,
      EndDate: stringDateRange.start,
    };
    return this.http
      .post<{ KPIValues: KPI }>(url, request)
      .pipe(map((response: { KPIValues: KPI }) => response.KPIValues));
  }
}
