import { Injectable } from '@angular/core';
import { BaseApi } from '../classes/base-api';
import { HttpClient } from '@angular/common/http';
import { BehaviorSubject, Observable } from 'rxjs';
import { combineLatest } from 'rxjs';
import { map, switchMap } from 'rxjs/operators';
import { Task } from '../models/task';

@Injectable({
  providedIn: 'root',
})
export class ApiTaskService extends BaseApi {
  organizationId$ = new BehaviorSubject(1);
  locationId$ = new BehaviorSubject(1);
  stringDateRange$ = new BehaviorSubject({ start: null, end: null });

  constructor(private http: HttpClient) {
    super();
  }

  search$ = combineLatest(
    this.organizationId$,
    this.locationId$,
    this.stringDateRange$
  ).pipe(
    switchMap(([organizationId, locationId, stringDateRange]) =>
      this.search(organizationId, locationId, stringDateRange)
    )
  );

  search(
    organizationId: number,
    locationId: number,
    stringDateRange: { start: string; end: string }
  ): Observable<Task[]> {
    const url = `${this.baseUrl}/searchtasks`;
    const request = {
      OrgID: organizationId,
      LocationID: locationId,
      StartDate: stringDateRange.start,
      EndDate: stringDateRange.end,
    };
    return this.http
      .post<{ TaskList: Task[] }>(url, request)
      .pipe(map((response: { TaskList: Task[] }) => response.TaskList));
  }

  getOverdueTasksByLocation(locationId: number): Observable<Task[]> {
    const url = `${this.baseUrl}/tasks/overdue/location/${locationId}`;
    return this.http
      .get<{ Tasks: Task[] }>(url)
      .pipe(map((response: { Tasks: Task[] }) => response.Tasks));
  }

  getOverdueTasksByOrganization(organizationId: number): Observable<Task[]> {
    const url = `${this.baseUrl}/tasks/overdue/org/${organizationId}`;
    return this.http
      .get<{ Tasks: Task[] }>(url)
      .pipe(map((response: { Tasks: Task[] }) => response.Tasks));
  }
}
