import { Component, Input, OnInit } from '@angular/core';
import { ChartEvent, ChartType } from 'ng-chartist';
import * as Chartist from 'chartist';

export interface Chart {
  type: ChartType;
  data: Chartist.IChartistData;
  options?: any;
  responsiveOptions?: any;
  events?: ChartEvent;
}

@Component({
  selector: 'xenon-ui-chart-donut',
  template: `
    <div class="pie-chart">
      <x-chartist
        [data]="chart.data"
        [type]="chart.type"
        [options]="chart.options"
        [responsiveOptions]="chart.responsiveOptions"
        [events]="chart.events"
      >
      </x-chartist>
    </div>
  `,
})
export class UiChartDonutComponent implements OnInit {
  @Input() percentage: number;

  chart: Chart;

  constructor() {}

  ngOnInit(): void {
    this.chart = {
      type: 'Pie',
      data: {
        series: [this.percentage, 100 - this.percentage],
      },
      options: {
        donut: true,
        height: 400,
        showLabel: false,
      },
    };
  }
}
