import { Component, Input, OnInit } from '@angular/core';
import { Observable, of } from 'rxjs';
import { ApiUserService } from '../../../shared/api/services/api-user.service';
import { User } from '../../../shared/api/models/user';

@Component({
  selector: 'xenon-team-list',
  template: `
    <table mat-table [dataSource]="users$ | async" class="mat-elevation-z8">
      <ng-container matColumnDef="Name">
        <th mat-header-cell *matHeaderCellDef>Name</th>
        <td mat-cell *matCellDef="let element">
          {{ element.FirstName }} {{ element.LastName }}
        </td>
      </ng-container>

      <ng-container matColumnDef="JobTitle">
        <th mat-header-cell *matHeaderCellDef>Title</th>
        <td mat-cell *matCellDef="let element">{{ element.JobTitle }}</td>
      </ng-container>

      <ng-container matColumnDef="Organization">
        <th mat-header-cell *matHeaderCellDef>Organization</th>
        <td mat-cell *matCellDef="let element">{{ element.OrgID }}</td>
      </ng-container>

      <ng-container matColumnDef="Email">
        <th mat-header-cell *matHeaderCellDef>Email</th>
        <td mat-cell *matCellDef="let element">{{ element.Email }}</td>
      </ng-container>

      <ng-container matColumnDef="Phone">
        <th mat-header-cell *matHeaderCellDef>Phone</th>
        <td mat-cell *matCellDef="let element">{{ element.Phone }}</td>
      </ng-container>

      <tr mat-header-row *matHeaderRowDef="displayedColumns"></tr>
      <tr mat-row *matRowDef="let row; columns: displayedColumns"></tr>
    </table>
    <pre>{{ users$ | async | json }}</pre>
  `,
})
export class TeamListComponent implements OnInit {
  displayedColumns: string[] = [
    'Name',
    'JobTitle',
    'Organization',
    'Email',
    'Phone',
  ];

  @Input() orgId: number;
  @Input() locationId: number;

  users$: Observable<User[]> = of([]);

  constructor(private userService: ApiUserService) {}

  ngOnInit(): void {
    this.users$ = this.userService.getUsersByOrganization(this.orgId);
  }
}
