import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';

export interface TaskFilterItem {
  active: boolean;
  label: string;
  value: string;
}

@Component({
  selector: 'xenon-tasks-filter',
  styleUrls: ['./tasks-filter.component.scss'],
  template: `
    <div fxLayout="row" class="task-filters">
      <div class="title-bar">
        <h2 class="title">Filters</h2>
        <mat-chip-list aria-label="Filter selection">
          <mat-chip
            [selected]="item.active"
            *ngFor="let item of items"
            [class.active]="item.active"
            (click)="changeFilter(item.value)"
          >
            {{ item.label }}
          </mat-chip>
        </mat-chip-list>
      </div>
    </div>
  `,
})
export class TasksFilterComponent implements OnInit {
  @Input() items: TaskFilterItem[];
  @Output() filterChange = new EventEmitter<TaskFilterItem[]>();

  constructor() {}

  ngOnInit(): void {}

  changeFilter(filter: string) {
    this.items.forEach((item) => (item.active = item.value === filter));
    this.filterChange.emit(this.items);
  }
}
