import { Component, OnDestroy, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Subscription } from 'rxjs';
import { ApiAuthenticationService } from '../../shared/api/services/api-authentication.service';
import { Router } from '@angular/router';
import { UtilLocalStorageService } from '../../shared/utils/services/util-local-storage.service';
import {
  UiNotificationService,
  NotificationType,
} from '../../shared/ui/services/ui-notification.service';
import { User } from '../../shared/api/models/user';

@Component({
  selector: 'xenon-page-login',
  styleUrls: ['./page-login.component.scss'],
  template: `
    <div class="login-register">
      <div class="login-register-box">
        <mat-card class="mat-elevation-z0">
          <mat-card-content>
            <form [formGroup]="form" (ngSubmit)="onSubmit()">
              <div class="text-center">
                <img
                  alt="logo"
                  class="login-logo"
                  src="./assets/login-logo.png"
                />
              </div>
              <div fxLayout="row wrap">
                <!-- col full-->
                <div fxFlex.gt-sm="100" fxFlex.gt-xs="100" fxFlex="100">
                  <mat-form-field>
                    <input
                      matInput
                      placeholder="Username"
                      [formControl]="form.controls['email']"
                    />
                  </mat-form-field>
                  <small
                    *ngIf="
                      form.controls['email'].hasError('required') &&
                      form.controls['email'].touched
                    "
                    class="support-text"
                    >Username is required.</small
                  >
                </div>
                <!-- col full-->
                <div fxFlex.gt-sm="100" fxFlex.gt-xs="100" fxFlex="100">
                  <mat-form-field>
                    <input
                      matInput
                      type="password"
                      placeholder="Password"
                      [formControl]="form.controls['password']"
                    />
                  </mat-form-field>
                  <small
                    *ngIf="
                      form.controls['password'].hasError('required') &&
                      form.controls['password'].touched
                    "
                    class="support-text"
                  >
                    Password is required.</small
                  >
                </div>
                <!-- col full-->
                <div fxFlex.gt-sm="100" fxFlex.gt-xs="100" fxFlex="100">
                  <button
                    mat-raised-button
                    color="primary"
                    type="submit"
                    [disabled]="!form.valid"
                  >
                    Login
                  </button>
                </div>
              </div>
            </form>
          </mat-card-content>
        </mat-card>
      </div>
    </div>
  `,
})
export class PageLoginComponent implements OnInit, OnDestroy {
  public form: FormGroup = Object.create(null);
  private subs: Subscription[] = [];

  constructor(
    private formBuilder: FormBuilder,
    private authenticationService: ApiAuthenticationService,
    private router: Router,
    private localStorage: UtilLocalStorageService,
    private notificationService: UiNotificationService
  ) {}

  ngOnDestroy(): void {
    this.subs.forEach((s) => s.unsubscribe());
  }

  ngOnInit() {
    // joshua.tune@gmail.com
    // TestAPI!
    this.form = this.formBuilder.group({
      email: ['', Validators.compose([Validators.required])],
      password: ['', Validators.compose([Validators.required])],
    });
  }

  onSubmit() {
    const login = this.authenticationService
      .login(this.form.controls.email.value, this.form.controls.password.value)
      .subscribe((currentUser: User) => {
        if (currentUser) {
          this.localStorage.setItem('user', currentUser);
          this.notificationService.show(
            NotificationType.success,
            'Login Successful!',
            `Welcome ${currentUser.FirstName} ${currentUser.LastName}`
          );
          this.router.navigate(['/home']);
        } else {
          this.notificationService.show(
            NotificationType.error,
            'Login Failed',
            'Invalid credentials, please try again'
          );
        }
      });
    this.subs.push(login);
  }
}
