import { Component, OnInit } from '@angular/core';
import { UtilLocalStorageService } from '../../shared/utils/services/util-local-storage.service';
import { ApiTaskService } from '../../shared/api/services/api-task.service';
import { BehaviorSubject, combineLatest, Observable, of } from 'rxjs';
import { Task } from '../../shared/api/models/task';
import { AppBase } from '../app-base';
import { TasksByDate } from '../../features/components/task-list-by-date/task-list-by-date.component';
import { map, switchMap } from 'rxjs/operators';
import { UtilDateToolsService } from '../../shared/utils/services/util-date-tools.service';
import { NgbDateStruct } from '@ng-bootstrap/ng-bootstrap';
import { TaskFilterItem } from '../../features/components/tasks-filter/tasks-filter.component';
import { UtilCloneService } from '../../shared/utils/services/util-clone.service';

@Component({
  selector: 'xenon-tasks',
  styleUrls: ['./tasks.component.scss'],
  template: `
    <div fxLayout="row" class="task-layout">
      <div fxFlex="336px" class="task-filters">
        <xenon-tasks-calender-filter
          [selectedDate]="date$ | async"
          (dateChange)="dateChange($event)"
        ></xenon-tasks-calender-filter>

        <mat-divider></mat-divider>

        <xenon-tasks-filter
          [items]="filterItems$ | async"
          (filterChange)="filterChange($event)"
        ></xenon-tasks-filter>

        <mat-divider></mat-divider>

        <div fxLayout="row">
          <div class="title-bar">
            <button mat-stroked-button (click)="reset()">Reset</button>
          </div>
        </div>
      </div>
      <div fxFlex class="task-details">
        <div
          class="page-header"
          fxLayout="row"
          fxLayoutAlign="space-between center"
        >
          <h2 class="title">Tasks</h2>
          <xenon-ui-string-search
            [searchString]="search$ | async"
            (searchChange)="searchChange($event)"
          >
          </xenon-ui-string-search>
        </div>

        <div class="page-body">
          <xenon-task-list-by-date
            id="task-list-by-date"
            [tasksByDate]="data$ | async"
            (dayClicked)="dayClicked($event)"
          >
          </xenon-task-list-by-date>
        </div>
      </div>
    </div>
  `,
})
export class TasksComponent extends AppBase implements OnInit {
  tasksByDateClone: TasksByDate[] = [];
  today = new Date();
  tasks$: Observable<Task[]>;
  tasksByDate: TasksByDate[] = [];

  items = [
    {
      active: true,
      label: 'All Tasks',
      value: 'all-tasks',
    },
    {
      active: false,
      label: 'Overdue',
      value: 'overdue',
    },
  ];

  date$: BehaviorSubject<NgbDateStruct> = new BehaviorSubject(null);
  filterItems$: BehaviorSubject<TaskFilterItem[]> = new BehaviorSubject(null);
  search$: BehaviorSubject<string> = new BehaviorSubject(null);

  tasksByDate$: Observable<TasksByDate[]> = this.taskService.search$.pipe(
    map((response: Task[]) => {
      const sortedTasks = response
        .slice()
        .sort((a, b) => (b.DueAt < a.DueAt ? 1 : -1));
      sortedTasks.forEach((task) => {
        const localDueAt = UtilDateToolsService.utcToLocal(task.DueAt);
        const dateString = UtilDateToolsService.convertToDateString(localDueAt);
        this._buildTasksByDate(localDueAt, dateString, task);
      });
      this.tasksByDateClone = UtilCloneService.deepClone(this.tasksByDate);

      return this.tasksByDate;
    })
  );

  data$: Observable<TasksByDate[]> = combineLatest(
    this.tasksByDate$,
    this.date$,
    this.filterItems$,
    this.search$
  ).pipe(
    switchMap(([tbd, date, filter, search]) => {
      // ToDo: Need to rethink this. Possibly use ngrx paradim
      tbd = UtilCloneService.deepClone(this.tasksByDateClone);

      // filter by date
      if (date) {
        const targetDate = `${date.year}-${
          date.month < 10 ? '0' + date.month : date.month
        }-${date.day < 10 ? '0' + date.day : date.day}`;
        tbd.forEach((item: TasksByDate) => {
          item.active = item.dateString === targetDate;
        });
      }

      // filter by search
      if (search && search.length) {
        tbd.forEach((item: TasksByDate) => {
          const filteredTasks = [];
          item.tasks.forEach((task: Task) => {
            if (task.Name.toLowerCase().indexOf(search.toLowerCase()) > -1) {
              filteredTasks.push(task);
            }
          });
          item.tasks = filteredTasks;
        });
      }

      // filter by filterItems
      if (filter) {
        const activeItem = filter.filter((i) => i.active)[0];
        tbd.forEach((item: TasksByDate) => {
          const filteredTasks = [];
          item.tasks.forEach((task: Task) => {
            const overdueCheck =
              UtilDateToolsService.utcToLocal(task.DueAt) < this.today;

            if (activeItem.value === 'all-tasks') {
              filteredTasks.push(task);
            } else if (activeItem.value === 'overdue' && overdueCheck) {
              filteredTasks.push(task);
            } else {
              filteredTasks.push(task);
            }
          });
          item.tasks = filteredTasks;
        });
      }

      return of(tbd);
    })
  );

  constructor(
    private localStorage: UtilLocalStorageService,
    private taskService: ApiTaskService
  ) {
    super(localStorage);

    this.taskService.stringDateRange$.next(this.stringDateRange);
    this.taskService.organizationId$.next(this.organizationId);
    this.taskService.locationId$.next(this.locationId);
    this.reset();
  }

  ngOnInit(): void {}

  reset() {
    this.date$.next({
      year: this.today.getFullYear(),
      month: this.today.getMonth() + 1,
      day: this.today.getDate(),
    });
    this.items.forEach((i) => (i.active = i.value === 'all-tasks'));
    this.filterItems$.next(this.items);
    this.search$.next('');
  }

  dateChange(event: NgbDateStruct): void {
    this.date$.next(event);
  }

  filterChange(event: TaskFilterItem[]) {
    this.filterItems$.next(event);
  }

  searchChange(event: string) {
    this.search$.next(event);
  }

  dayClicked(event: string) {
    const frag = event.split('-');
    const date = {
      day: parseInt(frag[2], 10),
      month: parseInt(frag[1], 10),
      year: parseInt(frag[0], 10),
    } as NgbDateStruct;
    this.date$.next(date);
  }

  _buildTasksByDate(date: Date, dateString: string, task: Task): void {
    if (this.tasksByDate.length) {
      let matchFound = false;
      let matchIndex = 0;
      this.tasksByDate.forEach((mt, index) => {
        if (mt.dateString === dateString) {
          matchFound = true;
          matchIndex = index;
        }
      });

      if (matchFound) {
        this.tasksByDate[matchIndex].tasks.push(task);
      } else {
        this.tasksByDate.push({
          dateString: dateString,
          tasks: [task],
          active: false,
        });
      }
    } else {
      this.tasksByDate.push({
        dateString: dateString,
        tasks: [task],
        active: false,
      });
    }
  }
}
