import { Component } from '@angular/core';
import { Title } from '@angular/platform-browser';
import { Router } from '@angular/router';

@Component({
  selector: 'xenon-sidebar',
  template: `
    <mat-list>
      <mat-list-item *ngFor="let item of items" [class.active]="item.active">
        <a
          class="nav-item nav-link"
          (click)="setDocTitle(item.title)"
          routerLink="{{ item.name }}"
        >
          <mat-icon color="iconic">{{ item.icon }}</mat-icon>
          {{ item.title }}
        </a>
      </mat-list-item>
    </mat-list>
  `,
})
export class SidebarComponent {
  items = [
    {
      active: true,
      name: 'home',
      title: 'Home',
      icon: 'home',
    },
    {
      active: false,
      name: 'tasks',
      title: 'Tasks',
      icon: 'check_box',
    },
    {
      active: false,
      name: 'drive',
      title: 'Drive',
      icon: 'folder',
    },
    {
      active: false,
      name: 'team',
      title: 'Team',
      icon: 'people',
    },
  ];

  constructor(private titleService: Title, private router: Router) {
    this.items.forEach((item) => {
      item.active = item.name === this.router.url.replace('/', '');
    });
  }

  setDocTitle(title: string): void {
    this.items.forEach((item) => {
      item.active = item.title === title;
    });
    this.titleService.setTitle(title);
  }
}
