import { Component, Input } from '@angular/core';
import { Task } from 'apps/x-client/src/shared/api/models/task';
import { UtilDateToolsService } from '../../../shared/utils/services/util-date-tools.service';

@Component({
  selector: 'xenon-task-by-date',
  styleUrls: ['./task-by-date.component.scss'],
  template: `
    <div fxLayout="row" class="task-by-date">
      <div fxFlex class="details">
        <div class="name">{{ task.Name }}</div>
        <div fxLayout="row" fxLayoutAlign="space-between">
          <span class="due-date"
            >Due at {{ task.DueAt | amDateFormat: 'LT' }}</span
          >
          <span>{{ task.ItemsComplete }} / {{ task.ItemsTotal }}</span>
        </div>
        <mat-progress-bar
          [color]="isActive() ? 'primary' : 'warn'"
          mode="determinate"
          value="{{ (task.ItemsComplete / task.ItemsTotal) * 100 }}"
        ></mat-progress-bar>
      </div>
      <div fxFlex="150px" class="progress">
        <div
          class="status text-center"
          [class.active]="isActive()"
          [class.overdue]="!isActive()"
        >
          Active
        </div>
      </div>
    </div>
  `,
})
export class TaskByDateComponent {
  today = new Date();
  @Input() task: Task;

  isActive() {
    return UtilDateToolsService.utcToLocal(this.task.DueAt) >= this.today;
  }
}
