import { User } from '../shared/api/models/user';
import { UtilLocalStorageService } from '../shared/utils/services/util-local-storage.service';

export const DEFAULT_STRING_DATE_RANGE = {
  start: '2020-09-01',
  end: '2020-09-30',
};

export const DEFAULT_DATE_RANGE = {
  start: new Date(2020, 8, 1),
  end: new Date(2020, 8, 30),
};

export class AppBase {
  currentUser: User;
  organizationId: number;
  locationId: number;
  stringDateRange: { start: string; end: string } = DEFAULT_STRING_DATE_RANGE;
  dateRange: { start: Date; end: Date } = DEFAULT_DATE_RANGE;

  constructor(localStorage: UtilLocalStorageService) {
    this.currentUser = localStorage.getItem('user');
    this.organizationId = this.currentUser.OrgID;
    this.locationId = this.currentUser.PrimaryLocationID;
  }
}
