import { Component, Input, OnInit } from '@angular/core';
import { ApiDriveService } from '../../../shared/api/services/api-drive.service';
import { Observable } from 'rxjs';
import { Drive } from '../../../shared/api/models/drive';

@Component({
  selector: 'xenon-drive-list',
  template: `
    <table mat-table [dataSource]="drives$ | async" class="mat-elevation-z0">
      <!-- ID Column -->
      <ng-container matColumnDef="ID">
        <th mat-header-cell *matHeaderCellDef>No.</th>
        <td mat-cell *matCellDef="let element">{{ element.ID }}</td>
      </ng-container>

      <!-- Name Column -->
      <ng-container matColumnDef="Name">
        <th mat-header-cell *matHeaderCellDef>Name</th>
        <td mat-cell *matCellDef="let element">{{ element.Name }}</td>
      </ng-container>

      <tr mat-header-row *matHeaderRowDef="displayedColumns"></tr>
      <tr mat-row *matRowDef="let row; columns: displayedColumns"></tr>
    </table>
    <pre>{{ drives$ | async | json }}</pre>
  `,
})
export class DriveListComponent implements OnInit {
  displayedColumns: string[] = ['ID', 'Name'];

  @Input() orgId: number;
  @Input() locationId: number;

  drives$: Observable<Drive[]>;

  constructor(private driveService: ApiDriveService) {}

  ngOnInit(): void {
    this.drives$ = this.driveService.getDrivesByOrg(this.orgId);
  }
}
