import { Component } from '@angular/core';
import { UtilLocalStorageService } from '../../shared/utils/services/util-local-storage.service';
import { Observable } from 'rxjs';
import { ApiTaskService } from '../../shared/api/services/api-task.service';
import { Task } from '../../shared/api/models/task';
import { ApiKpiService } from '../../shared/api/services/api-kpi.service';
import { KPI } from '../../shared/api/models/kpi';
import { UtilDateToolsService } from '../../shared/utils/services/util-date-tools.service';
import { AppBase } from '../app-base';

@Component({
  selector: 'xenon-home',
  template: `
    <div
      class="page-header"
      fxLayout="row"
      fxLayoutAlign="space-between center"
    >
      <h2 class="title">List Completion</h2>
      <div>
        <xenon-ui-date-range-picker
          [dateRange]="dateRange"
          (rangeChange)="dateRangeChanged($event)"
        ></xenon-ui-date-range-picker>
        <xenon-locations-select
          [organizationId]="organizationId"
          (locationChange)="locationChanged($event)"
        >
        </xenon-locations-select>
      </div>
    </div>

    <div class="page-body">
      <xenon-tasks-summary-donuts
        [kpi]="kpi$ | async"
      ></xenon-tasks-summary-donuts>
      <xenon-task-list [tasks]="tasks$ | async"></xenon-task-list>
    </div>
  `,
})
export class HomeComponent extends AppBase {
  tasks$: Observable<Task[]>;
  kpi$: Observable<KPI>;

  constructor(
    private localStorage: UtilLocalStorageService,
    private taskService: ApiTaskService,
    private kpiService: ApiKpiService
  ) {
    super(localStorage);

    this.tasks$ = this.taskService.search$;

    this.taskService.stringDateRange$.next(this.stringDateRange);
    this.taskService.organizationId$.next(this.organizationId);
    this.taskService.locationId$.next(this.locationId);

    this.kpi$ = this.kpiService.get$;

    this.kpiService.stringDateRange$.next(this.stringDateRange);
    this.kpiService.organizationId$.next(this.organizationId);
    this.kpiService.locationId$.next(this.locationId);
  }

  locationChanged(event: number): void {
    this.locationId = event;
    this.taskService.locationId$.next(event);
  }

  dateRangeChanged(event: { start: Date; end: Date }): void {
    const startStringDate = UtilDateToolsService.convertToDateString(
      event.start
    );
    const endStringDate = UtilDateToolsService.convertToDateString(event.end);
    const stringDateRange = { start: startStringDate, end: endStringDate };
    this.kpiService.stringDateRange$.next(stringDateRange);
    this.taskService.stringDateRange$.next(stringDateRange);
  }
}
