import { Component, EventEmitter, Input, Output } from '@angular/core';
import { Task } from '../../../shared/api/models/task';
import { NgbDateStruct } from '@ng-bootstrap/ng-bootstrap';

export interface TasksByDate {
  active: boolean;
  dateString: string;
  tasks: Task[];
}

@Component({
  selector: 'xenon-task-list-by-date',
  styleUrls: ['./task-list-by-date.component.scss'],
  template: `
    <div
      class="task-list-by-date"
      fxLayout="row"
      *ngFor="let mTask of tasksByDate"
    >
      <div fxFlex="82px" class="date">
        <div
          class="item"
          [class.active]="mTask.active"
          *ngIf="mTask"
          (click)="clickedDay(mTask.dateString)"
        >
          {{ mTask.dateString.split('-')[2] }}
        </div>
      </div>
      <div fxFlex class="tasks">
        <div *ngFor="let task of mTask.tasks" class="task-item">
          <xenon-task-by-date [task]="task"></xenon-task-by-date>
        </div>
      </div>
    </div>
  `,
})
export class TaskListByDateComponent {
  @Input() tasksByDate: TasksByDate[] = [];
  @Output() dayClicked = new EventEmitter<string>();

  clickedDay(dateString: string) {
    this.dayClicked.emit(dateString);
  }
}
