import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { Location } from '../models/location';
import { map } from 'rxjs/operators';
import { HttpClient } from '@angular/common/http';
import { BaseApi } from '../classes/base-api';

@Injectable({
  providedIn: 'root',
})
export class ApiLocationService extends BaseApi {
  constructor(private http: HttpClient) {
    super();
  }

  public get(id: number): Observable<Location> {
    const url = `${this.baseUrl}/location/${id}`;
    return this.http
      .get<{ Location: Location }>(url)
      .pipe(map((response: { Location: Location }) => response.Location));
  }
}
