import { Component, Input } from '@angular/core';
import { Observable, of } from 'rxjs';
import { Task } from '../../../shared/api/models/task';
import { ApiTaskService } from '../../../shared/api/services/api-task.service';

@Component({
  selector: 'xenon-task-list',
  template: `
    <table mat-table [dataSource]="tasks" class="mat-elevation-z0">
      <ng-container
        *ngFor="let colName of displayedColumns"
        matColumnDef="{{ colName }}"
      >
        <th mat-header-cell *matHeaderCellDef>{{ columnTitles[colName] }}</th>
        <td mat-cell *matCellDef="let element">{{ element[colName] }}</td>
      </ng-container>
      <tr mat-header-row *matHeaderRowDef="displayedColumns"></tr>
      <tr mat-row *matRowDef="let row; columns: displayedColumns"></tr>
    </table>
  `,
})
export class TaskListComponent {
  displayedColumns: string[] = [
    'Name',
    'ItemsComplete',
    'ItemsCompletedLate',
    'ItemsRemaining',
    'ItemsExpired',
  ];
  columnTitles = {
    Name: 'Name',
    ItemsComplete: 'Items Completed',
    ItemsCompletedLate: 'Items Completed Late',
    ItemsRemaining: 'Remaining Items',
    ItemsExpired: 'Missed Items',
  };

  @Input() tasks: Task[] = [];
}
