import { Component, EventEmitter, Input, Output } from '@angular/core';
import { NgbDateStruct } from '@ng-bootstrap/ng-bootstrap';

@Component({
  selector: 'xenon-tasks-calender-filter',
  styleUrls: ['./tasks-calender-filter.component.scss'],
  template: `
    <xenon-ui-calendar-datepicker
      [selectedDate]="selectedDate"
      (dateChange)="changeDate($event)"
    ></xenon-ui-calendar-datepicker>
  `,
})
export class TasksCalenderFilterComponent {
  @Input() selectedDate: NgbDateStruct;
  @Output() dateChange = new EventEmitter<NgbDateStruct>();

  changeDate($event): void {
    this.dateChange.emit($event);
  }
}
