import { Injectable } from '@angular/core';

export enum NotificationType {
  success = 'success',
  warning = 'warning',
  error = 'error',
  info = 'info',
}

@Injectable({
  providedIn: 'root',
})
export class UiNotificationService {
  constructor() {}

  show(type: NotificationType, title: string, message: string): void {
    alert(`type: ${type}, title: ${title}, message: ${message}`);
  }
}
