import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { Organization } from '../models/organization';
import { map } from 'rxjs/operators';
import { BaseApi } from '../classes/base-api';

@Injectable({
  providedIn: 'root',
})
export class ApiOrganizationService extends BaseApi {
  constructor(private http: HttpClient) {
    super();
  }

  public get(id: number): Observable<Organization> {
    const url = `${this.baseUrl}/org/${id}`;
    return this.http
      .get<{ Org: Organization }>(url)
      .pipe(map((response: { Org: Organization }) => response.Org));
  }
}
