import { Component } from '@angular/core';
import { ApiAuthenticationService } from '../../../shared/api/services/api-authentication.service';
import { Router } from '@angular/router';
import {
  UiNotificationService,
  NotificationType,
} from '../../../shared/ui/services/ui-notification.service';

@Component({
  selector: 'xenon-header',
  styles: ['.spacer { flex: 1 1 auto; }'],
  template: `
    <mat-toolbar id="main-header" color="primary">
      <img src="assets/logo-blue.png" alt="logo" class="header-logo" />
      <span class="spacer"></span>
      <button [matMenuTriggerFor]="profile" mat-icon-button class="m-r-5">
        <img src="assets/john-doe.png" alt="user" class="profile-pic" />
      </button>
      <mat-menu #profile="matMenu">
        <button mat-menu-item>
          <img src="assets/john-doe.png" alt="user" class="profile-pic" />
          Profile
        </button>
        <button mat-menu-item (click)="logout()">
          <mat-icon>exit_to_app</mat-icon> Sign Out
        </button>
      </mat-menu>
    </mat-toolbar>
  `,
})
export class HeaderComponent {
  constructor(
    private authenticationService: ApiAuthenticationService,
    private router: Router,
    private notificationService: UiNotificationService
  ) {}

  logout(): void {
    this.authenticationService.logout().subscribe((response) => {
      this.notificationService.show(
        NotificationType.success,
        'Bye bye',
        `See ya!`
      );
      this.router.navigate(['/login']);
    });
  }
}
