import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';
import { User } from '../models/user';
import { Response } from '../models/response';
import { BaseApi } from '../classes/base-api';
import { UtilLocalStorageService } from '../../utils/services/util-local-storage.service';

@Injectable({
  providedIn: 'root',
})
export class ApiAuthenticationService extends BaseApi {
  constructor(
    private http: HttpClient,
    private localStorageService: UtilLocalStorageService
  ) {
    super();
  }

  public login(email: string, password: string): Observable<User> {
    const url = `${this.baseUrl}/login`;
    const request = {
      Email: email,
      Key: password,
    };
    return this.http.post<{ CurrentUser: User }>(url, request).pipe(
      map((response: { CurrentUser: User }) => {
        return response.CurrentUser;
      })
    );
  }

  public logout(): Observable<Response> {
    const currentUser = this.localStorageService.getItem('user') as User;
    const url = `${this.baseUrl}/logout`;
    const request = {
      Email: currentUser.Email,
    };
    return this.http.post<Response>(url, request).pipe(
      map((response: Response) => {
        this.localStorageService.clear();
        return response;
      })
    );
  }

  public authorized(): Observable<Response> {
    const url = `${this.baseUrl}/authorized`;
    return this.http
      .get<Response>(url)
      .pipe(map((response: Response) => response));
  }
}
